<template>
    <div class="msite">
        <header>
            <router-link to="">
                <i class="el-icon-search i1"></i>
            </router-link>
            <router-link to="/home">
                <span class="title_text ellipsis dw">{{position ? position.name : ""}}</span>
            </router-link>
            <router-link to="/login" v-if="isShow1" class="wd">
                <span>登录|注册</span>
            </router-link>
            <router-link to="/profile" v-if="isShow2" class="wd">
                <i class="el-icon-user-solid"></i>
            </router-link>
        </header>
        <div class="msite_nav">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <div class="D1" v-for="item in data1" :key="item.id" @click="shopfood(item.title)">
                            <router-link to="/food">
                            <div class="D2">
                                <img :src="'https://cube.elemecdn.com'+item.image_url" class="img1">
                                <span class="s1">{{item.title}}</span>
                            </div>
                            </router-link>
                        </div>
                    </div>
                    <div class="swiper-slide">
                        <div class="D1" v-for="item in data2" :key="item.id" @click="shopfood(item.title)">
                            <router-link to="/food">
                            <div class="D2">
                                <img :src="'https://cube.elemecdn.com'+item.image_url" alt="" class="img1">
                                <span class="s1">{{item.title}}</span>
                            </div>
                            </router-link>
                        </div>
                    </div>
                </div>
                <!-- 如果需要分页器 -->
                <div class="swiper-pagination"></div>
            </div>
        </div>
        <div class="shop_list">
            <div class="shop_header">
                <span class="shop_icon"><i class="el-icon-s-shop"></i></span>
                <span class="shop_title">附近商家</span>
            </div>
            <div class="shoplist_container">
                <ul>
                    <li class="shop_li" v-for="s in shopping" @click="toShop(s)">
                        <img :src="'https://elm.cangdu.org/img/'+s.image_path" alt="" class="shop_img">
                        <div class="shop_right">
                            <div class="shop_detail"><h4 class="shop_detail_h4"><span class="shop_detail_s1">品牌</span>{{s.name}}</h4>
                                <ul>
                                    <li v-for="a in s.supports">
                                        <span>{{a.icon_name}}</span>
                                    </li>
                                </ul>
                            </div>
                            <div class="shop_detail2">
                                <div class="shop_detail2_d1">
                                    <el-rate
                                            v-model="s.rating"
                                            disabled
                                            text-color="#ff9900"
                                            class="shop_star">
                                    </el-rate>
                                    <span class="ratingnumber">{{s.rating}}</span>
                                    <div class="shop_detail2_d2">月售{{s.recent_order_num}}单</div>
                                </div>
                                <div class="shop_detail2_d3">
                                    <span class="shop_detail2_s1">蜂鸟专送</span>
                                    <span class="shop_detail2_s2">准时达</span>
                                </div>
                            </div>
                            <div class="shop_detail3">
                                <p class="shop_detail3_p1">￥{{s.float_minimum_order_amount}}起送<span>/</span>配送费约{{s.float_delivery_fee}}元</p>
                                <p class="shop_detail3_p2">
                                    <span>{{s.distance}}</span>
                                    <span>/</span>
                                    <span>{{s.order_lead_time}}</span>
                                </p>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>

        <footer>
            <router-link to=""><div class="f1"><i class="el-icon-eleme"></i><p>外卖</p></div></router-link>
            <router-link to="/search"><div><i class="el-icon-search"></i> <p>搜索</p></div></router-link>
            <router-link to=""><div><i class="el-icon-tickets"></i><p>订单</p></div></router-link>
            <router-link to="/profile"><div><i class="el-icon-user"></i> <p>我的</p></div></router-link>
        </footer>
    </div>
</template>

<script>
    import Swiper from "swiper";
    import "../../node_modules/swiper/css/swiper.css";
    export default {
        name: "Msite",
        data(){
          return {
              data1:[],
              data2:[],
              latitude:"",
              longitude:"",
              geohash:"",
              position:"",
              shopping:[],
              msite:[],
              isShow1:true,
              isShow2:false,
          }
        },
        mounted(){
            this.mySwiper()
        },
        methods:{
            mySwiper(){
                let mySwiper = new Swiper ('.swiper-container', {
                    direction: 'horizontal', // 垂直切换选项
                    loop: false, // 循环模式选项

                    // 如果需要分页器
                    pagination: {
                        el: '.swiper-pagination',
                    },
                    autoplay:2000,
                    autoHeight:true
                })
            },
            shopfood(p){
                this.$router.push({name:'f',params:{title:p}});
            },
            toShop(s){
                this.$router.push("/shop");
                localStorage.setItem("shop",JSON.stringify(s));
            }
        },
        created(){
            if(localStorage.getItem("user_id")){
                this.isShow1 = false;
                this.isShow2 = true;
            }else {
                this.isShow1 = true;
                this.isShow2 = false;
            }
            this.geohash = this.msite.geohash;
            this.msite = JSON.parse(localStorage.getItem("msite"));
            this.axios.get("/v2/index_entry").then(res=>{
                // console.log(res);
                for (let i = 0; i < res.length; i++){
                    if (i < 8) {
                        this.data1.push(res[i])
                    }else {
                        this.data2.push(res[i])
                    }
                }
                // console.log(this.data1,this.data2)
            }).catch(err=>{
                console.log(err);
            });

            // this.geohash = this.$route.params.geohash;
            // this.latitude = this.$route.params.latitude;
            // this.longitude = this.$route.params.longitude;

            this.axios.get("/v2/pois/"+this.msite.geohash
            ).then(res=>{
                // console.log(res);
                this.position = res;
            }).catch(err=>{
                console.log(err);
            })

            this.axios.get("/shopping/restaurants?",{
                params:{
                    latitude:this.msite.latitude,
                    longitude:this.msite.longitude
                }
            }).then(res=>{
                // console.log(res);
                this.shopping = res;
            }).catch(err=>{
                console.log(err);
            })
        }
    }
</script>

<style scoped>
    a {
        text-decoration: none;
    }
    .i1{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .dw{
        width: 8rem;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: .8rem;
        color: #fff;
        text-align: center;
    }
    .wd{
        right: .4rem;
        font-size: .65rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .title_text{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        display: block;
    }
    .msite_nav{
        padding-top: 2.1rem;
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        height: 10.6rem;
        box-sizing: border-box;
    }
    .swiper-container{
        width: 100%;
        height: auto;
        padding-bottom: .6rem;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        overflow: hidden;
        z-index: 1;
        box-sizing: border-box;
    }
    .swiper-wrapper{
        position: relative;
        width: 100%;
        height: 100%;
        z-index: 1;
        display: flex;
        transition-property: transform;
        box-sizing: content-box;
    }
    .swiper-pagination{
        left: 0;
        width: 100%;
        position: absolute;
        text-align: center;
        transition: .3s;
        transform: translateZ(0);
        z-index: 10;
        bottom: .2rem;
    }
    .D1{
        width: 25%;
        padding: .3rem 0;
        display: flex;
        justify-content: center;
        box-sizing: border-box;
    }
    .D2{
        padding: .3rem 0;
    }
    .img1{
        margin-bottom: .3rem;
        width: 1.8rem;
        height: 1.8rem;
    }
    .s1{
        text-align: center;
        font-size: .55rem;
        color: #666;
        display: block;
    }
    .swiper-slide{
        display: flex;
        flex-wrap: wrap;
    }
    .shop_list{
        margin-top: .4rem;
        border-top: .025rem solid #e4e4e4;
        background-color: #fff;
    }
    .shop_icon{
        fill: #999;
        margin-left: .6rem;
        vertical-align: middle;
        width: .6rem;
        height: .6rem;
    }
    .shop_title{
        color: #999;
        font: .55rem/1.6rem Microsoft YaHei;
    }
    .shoplist_container{
        background-color: #fff;
        margin-bottom: 2rem;
    }
    .shop_li{
        display: flex;
        border-bottom: .025rem solid #f1f1f1;
        padding: .7rem .4rem;
    }
    .shop_img{
        width: 2.7rem;
        height: 2.7rem;
        display: block;
        margin-right: .4rem;
    }
    .shop_right{
        flex: auto;
    }
    .shop_detail{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .shop_detail_h4{
        width: 8.5rem;
        color: #333;
        padding-top: .01rem;
        font: .65rem/.65rem PingFangSC-Regular;
        font-weight: 700;
    }
    .shop_detail_s1{
        display: inline-block;
        font-size: .5rem;
        line-height: .6rem;
        color: #333;
        background-color: #ffd930;
        padding: 0 .1rem;
        border-radius: .1rem;
        margin-right: .2rem;
    }
    .shop_detail>ul{
        display: flex;
        transform: scale(.8);
        margin-right: -.3rem;
    }
    .shop_detail>ul li{
        font-size: .5rem;
        color: #999;
        border: .025rem solid #f1f1f1;
        padding: 0 .04rem;
        border-radius: .08rem;
        margin-left: .05rem;
    }
    .shop_detail2{
        display: flex;
        justify-content: space-between;
        height: .7rem;
        margin-top: .3rem;
        font-size: 0.83em;
    }
    .shop_detail2_d1{
        display: flex;
        justify-content: flex-start;
    }
    .shop_detail2_d2{
        transform: scale(.8);
        margin-left: .1rem;
        font-size: .4rem;
        color: #666;
        padding-top: .3rem;
    }
    .shop_detail2_d3{
        display: flex;
        align-items: center;
        transform: scale(.7);
        min-width: 5rem;
        justify-content: flex-end;
        margin-right: -.8rem;
    }
    .shop_detail2_s1{
        color: #fff;
        background-color: #3190e8;
        border: .025rem solid #3190e8;
        font-size: .4rem;
        padding: .04rem .08rem 0;
        border-radius: .08rem;
        margin-left: .08rem;
    }
    .shop_detail2_s2{
        color: #3190e8;
        border: .025rem solid #3190e8;
        font-size: .4rem;
        padding: .04rem .08rem 0;
        border-radius: .08rem;
        margin-left: .08rem;
    }
    .shop_detail3{
        margin-top: .52rem;
        display: flex;
        justify-content: space-between;
        font-size: .5rem;
        color: #333;
    }
    .shop_detail3_p1{
        transform: scale(.9);
        font-size: .5rem;
        color: #666;
    }
    .shop_detail3_p2{
        transform: scale(.9);
    }
    .shop_detail3_p2>span:nth-child(1){
        color: #999;
    }
    .shop_detail3_p2>span:nth-child(2){
        color: #ccc;
    }
    .shop_detail3_p2>span:nth-child(3){
        color: #3190e8;
    }

    footer{
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 1.95rem;

        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    footer div{
        width: 25%;
        height: 1.95rem;
        text-align: center;
        align-items: center;
        float: left;
    }

    footer i{
        width: .8rem;
        height: .8rem;
        margin-top: .3rem;
        margin-right: .2rem;
        color: #666;
    }
    .f1 i{
        color:#3190e8;
    }
    footer p{
        font-size: .45rem;
        color: #666;
    }
</style>
<style>
    .shop_detail2_d1 i{
        margin-right: -0.1rem;
        font-size: 0.4rem;
        margin-bottom: -0.1rem;
    }
    .ratingnumber{
        margin-left: 0.2rem;
        font-size: 0.4rem;
        padding-top: 0.27rem;
        color: #ff6000;
    }
</style>